.container {
    display: flex;
}

.dialog {
    position: relative;
    padding: 15px 15px 5px;
    margin: 5px 0;
    color: #000;
    background: #f3961c;
    border-radius: 10px;
    background: linear-gradient(top, #f9d835, #f3961c);

}

.arrow {
    position: absolute;
    width: 0;
    bottom: 42px;
    left: -16px;
    height: 0;
    border-color: #f3961c;
}

.arrow:after {
    content: "";
    position: absolute;
    border: 0 solid transparent;
    border-top: 9px solid #f3961c;
    border-radius: 0 20px 0;
    width: 15px;
    height: 30px;
    transform: rotate(145deg);

}

.avatar {
    margin: 40px 10px 0;
    width: 5%;
    height: 5%;
    border-radius: 200px;
}

.time {
    color: #282c34;
    opacity: .4;
    font-weight: bold;
    text-align: right;
    margin-top: 10px;
}

.name {
    color: #282c34;
    font-weight: bold
}
